import React,{Fragment,useState,useEffect} from 'react';
import {Link} from 'react-router-dom';
import './Navbar.css';
import Button from '../utils/Button';
const Navbar = () => {
  const [click,setClick] = useState(false);
  const [button,setButton] = useState(true)
  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  const showButton = () => {
    if(window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  }
  useEffect(() => {
    showButton()
  },[])
  window.addEventListener('resize',showButton);
  return (
    <Fragment>
      <nav className="navbar">
        <div className="navbar-container">
          <Link to="/Trvl" className="navbar-logo" onClick={closeMobileMenu}>
            TRVL <i className="fab fa-typo3"/>
          </Link>
          <div className="menu-icon" onClick={handleClick}>
            <i className={click ? 'fas fa-times' : 'fas fa-bars'}/>
          </div>
          <ul className={click ? 'nav-menu active' : 'nav-menu'}>
            <li className="nav-item">
              <Link to="/Trvl" className="nav-links" onClick={closeMobileMenu}>
                Home
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/Trvl/services" className="nav-links" onClick={closeMobileMenu}>
                Services
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/Trvl/products" className="nav-links" onClick={closeMobileMenu}>
                Products
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/Trvl/sign-up" className="nav-links-mobile" onClick={closeMobileMenu}>
                Sign Up
              </Link>
            </li>
          </ul>
          {button && <Button buttonStyle="btn--outline">Sign Up</Button>}
        </div>
      </nav>
    </Fragment>
  )
}

export default Navbar;